import 'normalize.css';
import '../assets/styles/thank.main.scss';
const init = () => {
	const button = document.body.querySelector('#homepageReturn');
	button.addEventListener('click', function(e) {
		e.preventDefault();
		const direct = this.getAttribute('href');
		window.location.pathname = `${direct}`;
	});
};
window.onload = init;


